.line {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.input {
  height: 50px;
  width: 100%;
  font-size: 16px;
  padding: 4px 11px;
  color: rgba(0, 0, 0, 0.88);
  line-height: 1.5714285714285714;
  font-weight: 300;
  border: 1px solid var(--gray);
  outline-color: rgb(22 119 255 / 56%);
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}

.content {
  width: 100%;
}

.flag {
  display: flex;
  align-items: center;
  padding: 0 10px;
  min-width: 110px;
  height: 50px;
  border: 1px solid var(--gray);
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}
